.status.status--edit {
  max-width: initial;
  margin-bottom: 16px;
  padding-left: 0;
}

.exp-profile {
  width: 100%;
}

.exp-profile__top {
  height: 344px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
}
.exp-profile__top .about__sett__left {
  position: absolute;
  left: 24px;
  bottom: 16px;
  padding: 8px;
  padding-right: 12px;
  background-color: #fff;
}

.exp-profile__avatar {
  width: 230px;
  height: 230px;
  border-radius: 100%;
  border: 6px solid #F1F6FA;
  left: 50%;
  bottom: 0;
  background-size: cover;
  background-position: center;
  position: absolute;
  transform: translate(-50%, 39px);
}

.exp-profile__info {
  display: flex;
  justify-content: center;
  margin-top: 18px;
  align-items: flex-start;
  margin-bottom: 19px;
}

.exp-profile__rate {
  background: #092C9E;
  border-radius: 2px;
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #F1F6FA;
  padding-left: 8px;
  padding-right: 31px;
  display: flex;
  align-items: center;
}
.exp-profile__rate svg {
  margin-right: 31px;
}

.exp-profile__rate.tooltips-wrap .tooltips {
  left: 0;
  transform: translateX(0);
  width: 450px;
  font-size: 14px;
  line-height: 24px;
  font-family: "Mulish";
  font-weight: 300;
  bottom: 120%;
}
.exp-profile__rate.tooltips-wrap .tooltips a {
  display: inline-block;
}
.exp-profile__rate.tooltips-wrap .tooltips a:hover {
  text-decoration: underline;
}

.exp-profile__rate.tooltips-wrap.active .tooltips {
  display: block;
}

.exp-profile__col {
  flex: 1;
  display: flex;
}

.exp-profile__person {
  margin-top: 26px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-right: 15px;
  padding-left: 15px;
}

.exp-profile__person__name {
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #0A243F;
  text-align: center;
  margin-bottom: 8px;
}

.exp-profile__person__location {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 8px;
  color: #737B7D;
  text-align: center;
}

.exp-profile__person__prof {
  text-align: center;
  font-size: 24px;
  line-height: 36px;
  color: rgba(0, 0, 0, 0.8);
}

.exp-profile__person__label {
  display: flex;
  align-items: flex-start;
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #0A243F;
  margin-bottom: 17px;
}
.exp-profile__person__label img {
  margin-right: 18px;
}

.exp-profile__person__rate {
  margin-top: 17px;
  display: flex;
  justify-content: center;
}
.exp-profile__person__rate svg {
  margin-right: 12px;
}

.exp-profile__person__label-wrap {
  margin-top: 5px;
}

.exp-profile__col--right {
  justify-content: flex-end;
}
.exp-profile__col--right ul {
  margin: 0;
  padding: 0;
}
.exp-profile__col--right li {
  list-style: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  margin-bottom: 4px;
  font-weight: bold;
  font-size: 18px;
  line-height: 28px;
  color: rgba(0, 0, 0, 0.8);
}
.exp-profile__col--right li img {
  margin-right: 8px;
}

.exp-profile__tabs__content {
  display: none;
  background-color: #fff;
  padding-left: 29px;
  padding-top: 10px;
}

.exp-profile__tabs__content.active {
  display: block;
}

.exp-profile__tabs__nav {
  display: flex;
  margin: 0;
  padding: 0;
}
.exp-profile__tabs__nav li {
  padding: 22px 15px 15px;
  border-radius: 10px 10px 0px 0px;
  text-align: center;
  font-size: 24px;
  line-height: 36px;
  color: #0A243F;
  list-style: none;
  flex: 1;
  cursor: pointer;
}
.exp-profile__tabs__nav li:hover {
  color: #092C9E;
}
.exp-profile__tabs__nav li.active {
  background-color: #fff;
  color: #092C9E;
}

.exp-profile__row {
  padding-top: 39px;
  margin-bottom: 20px;
  padding-bottom: 39px;
  padding-right: 60px;
  padding-left: 30px;
  border-bottom: 1px solid #A1A4B1;
}
.exp-profile__row .exp-content__title {
  padding-left: 0;
  border-bottom: 0;
}
.exp-profile__row .exp-content__title h3 {
  margin-bottom: 24px;
}

.exp-profile__row:last-child {
  border-bottom: 0;
}

.exp-profile__skills {
  display: flex;
  flex-wrap: wrap;
  max-width: 1040px;
}

.exp-profile__slills__item {
  padding: 8px 16px;
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #3C64B1;
  background: #DBEEFB;
  border-radius: 20px;
  margin-right: 14px;
  margin-bottom: 12px;
}

.exp-profile__bot-btn {
  display: flex;
  justify-content: flex-end;
  margin-top: 24px;
}

.exp-profile__bot-btn--center {
  justify-content: center;
  margin-top: 56px;
}

.exp-profile__uslugi__wrap {
  display: flex;
}

.exp-profile__uslugi__img {
  margin-right: 78px;
  flex: 0 0 277px;
  width: 277px;
  height: 185px;
  background-size: cover;
  background-position: center;
}

.exp-profile__uslugi__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: #18A0FB;
  margin-bottom: 12px;
  display: inline-block;
}

.exp-profile__uslugi__tags {
  display: flex;
  flex-wrap: wrap;
}

.exp-profile__uslugi__tags__item {
  font-weight: bold;
  font-size: 15px;
  line-height: 24px;
  color: #737B7D;
  margin-right: 70px;
  margin-bottom: 12px;
}

.exp-profile__uslugi__text {
  font-size: 15px;
  line-height: 24px;
  color: #000000;
}

.exp-profile__uslugi__wrap2 {
  max-width: 701px;
}

.skills__list ul {
  margin: 0;
  padding: 0;
}
.skills__list li {
  margin-bottom: 6px;
  list-style: none;
  display: flex;
  align-items: flex-start;
}
.skills__list li span {
  min-width: 175px;
  margin-right: 38px;
}

.skills__text p {
  font-size: 15px;
  line-height: 24px;
  color: #000000;
  margin: 10px 0;
  max-width: 826px;
}

.exp-profile__portfolio {
  margin-bottom: 30px;
}

.exp-profile__portfolio__top {
  display: flex;
  margin-bottom: 38px;
}
.exp-profile__portfolio__top a {
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: rgba(0, 0, 0, 0.8);
  margin-right: 82px;
}
.exp-profile__portfolio__top a:hover {
  color: #2D9CDB;
}
.exp-profile__portfolio__top a.active {
  color: #2D9CDB;
}

.exp-profile__portfolio__wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -36px;
  margin-bottom: -97px;
}

.exp-profile__portfolio__item {
  margin: 0 36px;
  width: calc(25% - 72px);
  padding-bottom: 13%;
  background-size: cover;
  background-position: center;
  margin-bottom: 97px;
}

.exp-profile__reviews {
  max-width: 944px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.exp-profile__reviews__item {
  padding-bottom: 6px;
  margin-bottom: 26px;
  border-bottom: 1px solid #A1A4B1;
}

.exp-profile__reviews__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 19px;
}

.exp-profile__reviews__name {
  font-family: "Mulish";
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.1px;
}

.exp-profile__reviews__price {
  margin-left: 15px;
  font-weight: bold;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.3px;
  color: #A3A3A3;
}
.exp-profile__reviews__price span {
  margin-left: 26px;
}

.exp-profile__reviews__rate {
  margin-bottom: 19px;
}
.exp-profile__reviews__rate svg {
  margin-right: 8px;
}

.exp-profile__reviews__text {
  font-family: "Mulish";
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.2px;
  color: #373F41;
  margin-bottom: 18px;
}

.exp-profile__reviews__author {
  font-size: 15px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.8);
}

.exp-profile__reviews__author:hover {
  text-decoration: underline;
  color: rgba(0, 0, 0, 0.8);
}

.exp-profile__uslugi__list {
  padding-bottom: 45px;
}
.exp-profile__uslugi__list .exp-profile__uslugi {
  padding-top: 40px;
  padding-bottom: 64px;
  border-bottom: 1px solid #A1A4B1;
}

.exp-profile__reviews-all {
  padding-top: 100px;
  padding-bottom: 100px;
}